            padding-bottom: 50px;
            flex-basis: calc( 100% / 1 - 25px);
        }
        &__item:not(:last-child) {
            margin-right: 25px;
        }
        &__item-title {
            margin-bottom: 70px;
            font-size: 32px;
            line-height: 1.13;
             
        }
        &__item-title::after {        
